<template>
  <svg v-if="icon" :class="['svg-icon', className, colourClass, sizeClass, modifierClass]"><use :xlink:href="`#${this.icon}`"></use></svg>
</template>

<script>
export default {
  name: 'icon',
  props: {
    viewBox: {
      type: String
    },
    icon: {
      type: String,
      default: ''
    },
    className: {
      type: String
    },
    colour: {
      type: String
    },
    size: {
      type: String
    },
    modifier: {
      type: String
    }
  },
  data() {
    return {
      spinning: false
    };
  },
  computed: {
    colourClass() {
      return this.colour ? this.colour : '';
    },
    sizeClass() {
      return this.size ? `svg-icon--${this.size}` : '';
    },
    modifierClass() {
      return this.modifier ? `svg-icon--${this.modifier}` : '';
    }
  }
};
</script>
