<template>
  <div class="inline-text">
    <icon v-if="icon" :icon="icon" size="100"></icon>
    <span>
      <span v-html="msg"></span>
      <template v-if="link"
        >&ensp;<hyperlink
          :url="link"
          :text="linkText"
          :target="linkTarget"
          @click="handleHyperlinkClick($event)"
        ></hyperlink
      ></template>
    </span>
  </div>
</template>

<script>
import Icon from './../icon';
import Hyperlink from './../hyperlink';

export default {
  name: 'inline-text',
  components: {
    Icon,
    Hyperlink
  },
  props: {
    icon: {
      type: String
    },
    link: {
      type: String
    },
    linkText: {
      type: String
    },
    linkTarget: {
      type: String
    },
    msg: {
      type: String,
      required: true
    }
  },
  methods: {
    handleHyperlinkClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>
