<template>
  <div :class="['modal', modalVideoClass, modalAlertClass, openClass]">
    <ikea-button
      v-if="modalType === 'video' || modalType === 'informational'"
      type="plain"
      icon="dtd-cross"
      @click="closeModal()"
      :light="!!(modalType === 'video' || light)"
      :class="modalType === 'video' ? 'modal__video-close' : 'modal__close'"
    ></ikea-button>

    <div v-if="isOpen && title" class="modal__title">
      <h4 v-html="title"></h4>
    </div>

    <slot></slot>

    <div v-if="modalBtns" class="modal__buttons">
      <template v-for="(button, index) in modalBtns">
        <ikea-button
          :key="index"
          :text="button.text"
          :type="button.type"
          :light="button.light"
          @click="closeModal()"
        ></ikea-button>
      </template>
    </div>
  </div>
</template>

<script>
import Button from './../button';
export default {
  name: 'modal-window',
  components: {
    'ikea-button': Button
  },
  props: {
    isOpen: {
      type: Boolean
    },
    modalBtns: {
      type: Array
    },
    modalType: {
      type: String
    },
    title: {
      type: String
    },
    light: {
      type: Boolean
    }
  },
  computed: {
    modalVideoClass() {
      return this.modalType === 'video' ? 'modal__video' : null;
    },
    modalAlertClass() {
      return this.modalType === 'alert' ? 'modal__alert' : null;
    },
    openClass() {
      return this.isOpen ? 'zoom-enter' : 'zoom-leave';
    }
  },
  watch: {
    isOpen() {
      if (this.isOpen) {
        this.$emit('opened');
      } else {
        this.$emit('closed');
      }
    }
  },
  methods: {
    closeModal() {
      this.$emit('close-modal');
    }
  }
};
</script>
