<template>
  <div
    :class="['banner', lightClass, positionClass, dismissableClass, openClass]"
  >
    <div class="banner__container">
      <div class="banner__content">
        <h4 v-if="title" class="banner__title" v-html="title"></h4>
        <p v-if="message">
          <span v-html="message"></span>&nbsp;
          <Hyperlink
            v-if="linkCtaProps"
            :text="linkCtaProps.text"
            :url="linkCtaProps.url"
            :target="linkCtaProps.target"
            :icon="linkCtaProps.icon"
            :icon-position="linkCtaProps.iconPosition"
            :icon-size="linkCtaProps.iconSize"
            :colour="light ? null : 'white'"
          ></Hyperlink>
        </p>
        <slot></slot>
      </div>
      <div class="banner__actions" v-if="button1props || button2props">
        <ikea-button
          v-if="button1props"
          :text="button1props.text"
          :icon="button1props.icon"
          :disabled="button1props.disabled"
          :light="!light"
          :type="button1props.type"
          :icon-position="button1props.iconPosition"
          :class-name="button1props.className"
          @click="handleButtonClick($event)"
        ></ikea-button>
        <ikea-button
          v-if="button2props"
          :text="button2props.text"
          :icon="button2props.icon"
          :disabled="button2props.disabled"
          :light="!light"
          :type="button2props.type"
          :icon-position="button2props.iconPosition"
          :class-name="button2props.className"
          @click="handleButtonClick($event)"
        ></ikea-button>
      </div>
      <ikea-button
        v-if="dismissable"
        text="Close"
        :light="!light"
        class-name="banner__close-btn"
        type="plain"
        icon="cross-24"
        @click="close($event)"
      ></ikea-button>
    </div>
  </div>
</template>

<script>
import Hyperlink from './../hyperlink';
import Button from './../button';

export default {
  name: 'banner',
  components: {
    Hyperlink,
    'ikea-button': Button
  },
  props: {
    title: {
      type: String
    },
    message: {
      type: String
    },
    position: {
      type: String,
      default: 'bottom'
    },
    version: {
      type: String
    },
    visible: {
      type: Boolean
    },
    disable: {
      type: Boolean
    },
    dismissable: {
      type: Boolean,
      required: true
    },
    enterTimer: {
      type: Number,
      default: 0
    },
    exitTimer: {
      type: Number,
      default: 0
    },
    linkCtaProps: {
      type: Object
    },
    button1props: {
      type: Object
    },
    button2props: {
      type: Object
    }
  },
  data() {
    return {
      light: this.version === 'subtle',
      open: this.enterTimer === 0 && !this.disable,
      exitTimeout: null,
      enterTimeout: null
    };
  },
  watch: {
    disable() {
      if (this.disable === true) {
        this.open = false;
      } else {
        this.initiateBanner();
      }
    }
  },
  computed: {
    lightClass() {
      return this.light ? 'banner--light' : 'banner--dark';
    },
    positionClass() {
      return this.position ? `banner--${this.position}` : '';
    },
    dismissableClass() {
      return this.dismissable ? 'banner--dismissable' : '';
    },
    openClass() {
      return this.open ? 'banner--open' : '';
    }
  },
  mounted() {
    if (!this.disable) {
      this.initiateBanner();
    }
  },
  beforeDestroy() {
    if (this.enterTimeout || this.exitTimeout) {
      clearTimeout(this.enterTimeout);
      clearTimeout(this.exitTimeout);
    }
  },
  methods: {
    close(e) {
      e.preventDefault();
      this.open = false;
    },
    initiateBanner() {
      if (this.enterTimer !== 0 || !this.disable) {
        this.enterTimeout = setTimeout(() => {
          this.open = true;
        }, this.enterTimer);
      }

      if (this.exitTimer !== 0) {
        this.exitTimeout = setTimeout(() => {
          this.open = false;
        }, this.exitTimer + this.enterTimer);
      }
    },
    handleButtonClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>
