        <Hyperlink :url="item.url" :text="item.label"></Hyperlink>
      </li>
      <li :key="index" v-else>{{ item.label }}</li>
    </template>
  </ul>
</template>

<script>
import Hyperlink from './../hyperlink';
export default {
  name: 'alert-list-items',
  components: {
    Hyperlink
  },
  props: {
    items: {
      type: Array
    }
  }
