<template>
  <div :class="['form-field', validClass, successClass]">
    <input-field
      v-if="children && children.childType === 'input-field'"
      :id="children.id"
      :label="children.label"
      :placeholder="children.placeholder"
      :req="children.req"
      :type="children.type"
      :disabled="children.disabled"
      :icon="children.icon"
      :iconClickDescription="children.iconClickDescription"
      :iconPosition="children.iconPosition"
      :max-length="children.maxLength"
      :name="children.name"
      :value="children.value"
      :mask="children.mask"
      :returnMasked="children.returnMasked"
      @change="handleFormFieldChange($event)"
      @blur="handleFormFieldBlur"
      @input="handleFormFieldInput"
      @focus="handleInputFieldFocus"
    ></input-field>

    <checkbox
      v-if="children && children.childType === 'checkbox'"
      :id="children.id"
      :name="children.name"
      :value="children.value"
      :text="children.text"
      :disabled="children.disabled"
      :checked="children.checked"
      :labelLink="children.labelLink"
      :labelLinkText="children.labelLinkText"
      @change="handleFormFieldChange($event)"
    ></checkbox>
    <checkbox-group
      v-if="children && children.childType === 'checkbox-group'"
      :name="children.name"
      :list="children.list"
      @change="handleFormFieldChange($event)"
    ></checkbox-group>
    <checkbox-group-flex
      v-if="children && children.childType === 'checkbox-group-flex'"
      :name="children.name"
      :list="children.list"
      @change="handleFormFieldChange($event)"
    ></checkbox-group-flex>

    <radio-button
      v-if="children && children.childType === 'radio-button'"
      :id="children.id"
      :name="children.name"
      :value="children.value"
      :text="children.text"
      :disabled="children.disabled"
      :checked="children.checked"
      :labelLink="children.labelLink"
      :labelLinkText="children.labelLinkText"
      @change="handleFormFieldChange($event)"
    ></radio-button>
    <radio-button-group
      v-if="children && children.childType === 'radio-button-group'"
      :name="children.name"
      :list="children.list"
      @change="handleFormFieldChange($event)"
    ></radio-button-group>

    <dropdown
      v-if="children && children.childType === 'dropdown'"
      :id="children.id"
      :label="children.label"
      :req="children.req"
      :disabled="children.disabled"
      :size="children.size"
      :dropdownItems="children.dropdownItems"
      :isGroup="children.isGroup"
      :groupName="children.groupName"
      :selected-item="children.selectedItem"
      @change="handleFormFieldChange($event)"
    ></dropdown>

    <search-field
      v-if="children && children.childType === 'search-field'"
      :id="children.id"
      :label="children.label"
      :placeholder="children.placeholder"
      :disabled="children.disabled"
      :type="children.type"
      :loading="children.loading"
      @change="handleFormFieldChange($event)"
      @blur="handleFormFieldBlur"
      @input="handleFormFieldInput"
      @click="handleFormFieldClick($event)"
    ></search-field>

    <text-area
      v-if="children && children.childType === 'text-area'"
      :id="children.id"
      :label="children.label"
      :value="children.value"
      :placeholder="children.placeholder"
      :req="children.req"
      :max-length="children.maxLength"
      :disabled="children.disabled"
      :base-height="children.baseHeight"
      :max-height="children.maxHeight"
      :described-by-id="children.describedById"
      @change="handleFormFieldChange($event)"
      @input="handleFormFieldInput"
      @blur="handleFormFieldBlur"
    ></text-area>

    <quantitiy-field
      v-if="children && children.childType === 'quantitiy-field'"
      :id="children.id"
      :max="children.max"
      :value="children.value"
      :disabled="children.disabled"
      :increase-btn-txt="children.increaseBtnTxt"
      :decrease-btn-txt="children.decreaseBtnTxt"
      @change="handleFormFieldChange($event)"
    ></quantitiy-field>

    <inline-text
      v-if="!valid && shouldValidate && error"
      :icon="error.icon ? error.icon : 'message-error-bg-16'"
      :type="error.type ? error.type : 'error'"
      :msg="error.msg"
      :link-text="error.linkText"
      :link="error.link"
      :link-target="error.linkTarget"
    ></inline-text>
    <inline-text
      v-else-if="valid && shouldValidate && success"
      :icon="success.icon ? success.icon : 'message-confirmation-bg-16'"
      :type="success.type ? success.type : 'success'"
      :msg="success.msg"
      :link-text="success.linkText"
      :link="success.link"
      :link-target="success.linkTarget"
    ></inline-text>
    <inline-text
      v-else-if="fieldHelper && fieldHelper"
      :icon="fieldHelper.icon"
      :msg="fieldHelper.msg"
      :link-text="fieldHelper.linkText"
      :link="fieldHelper.link"
      :link-target="fieldHelper.linkTarget"
    ></inline-text>
  </div>
</template>

<script>
import InlineText from './../inline-text';
import InputField from './../input-field';
import Checkbox from './../checkbox';
import CheckboxGroup from './../checkbox-group';
import CheckboxGroupFlex from './../checkbox-group-flex';
import RadioButton from './../radio-button';
import RadioButtonGroup from './../radio-button-group';
import Dropdown from './../dropdown';
import SearchField from './../search-field';
import TextArea from './../text-area';
import QuantitiyField from './../quantity-field';

export default {
  name: 'form-field',
  components: {
    InlineText,
    InputField,
    Checkbox,
    CheckboxGroup,
    CheckboxGroupFlex,
    RadioButton,
    RadioButtonGroup,
    Dropdown,
    SearchField,
    TextArea,
    QuantitiyField
  },
  props: {
    children: {
      type: Object,
      required: true
    },
    error: {
      type: Object
    },
    success: {
      type: Object
    },
    fieldHelper: {
      type: Object
    },
    shouldValidate: {
      type: Boolean,
      default: false
    },
    valid: {
      type: Boolean
    }
  },
  computed: {
    validClass() {
      return !this.valid && this.shouldValidate
        ? this.error.type
          ? this.error.type
          : 'error'
        : '';
    },
    successClass() {
      if (this.success) {
        return this.valid && this.shouldValidate
          ? this.success.type
            ? this.success.type
            : 'success'
          : '';
      }
    }
  },
  methods: {
    handleFormFieldChange(event) {
      this.$emit('change', event);
    },
    handleFormFieldBlur(event) {
      this.$emit('blur', event);
    },
    handleFormFieldInput(event) {
      this.$emit('input', event);
    },
    handleInputFieldFocus(event) {
      this.$emit('focus', event);
    },
    handleFormFieldClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>

<style src="./styles.scss" lang="scss"></style>
