<template>
  <span :class="['loading-spinner', loadingClass]">
    <span class="loading-spinner__label">Loading...</span>
  </span>
</template>

<script>
export default {
  name: 'loading-spinner',
  props: {
    light: {
      type: Boolean
    }
  },
  computed: {
    loadingClass() {
      return this.light ? 'loading-spinner--light' : 'loading-spinner--dark';
    }
  }
};
</script>
