<template>
  <a
    :target="target"
    :href="url"
    :class="[
      className,
      colourClass,
      fontSizeClass,
      iconLeadingClass,
      iconTrailingClass,
      disabledClass
    ]"
    :role="roleState"
    :aria-disabled="disabledState"
    :aria-controls="controlState"
    :aria-label="labelState"
    :aria-labelledby="labelledByState"
    @click="handleHyperlinkClick($event)"
  >
    <icon
      v-if="iconPosition === 'leading' && icon"
      :colour="colour"
      :icon="icon"
      :size="iconSize"
    ></icon>
    <template v-if="text && text.length > 0">{{ text }}</template>
    <slot></slot>
    <icon
      v-if="(iconPosition === 'trailing' && icon) || (!iconPosition && icon)"
      :colour="colour"
      :icon="icon"
      :size="iconSize"
    ></icon>
  </a>
</template>

<script>
import Icon from './../icon';
export default {
  name: 'hyperlink',
  components: {
    Icon
  },
  props: {
    text: {
      type: String
    },
    className: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    url: {
      type: String,
      required: true
    },
    colour: {
      type: String
    },
    fontSize: {
      type: String
    },
    button: {
      type: Boolean,
      default: false
    },
    target: {
      type: String,
      default: '_self'
    },
    icon: {
      type: String
    },
    iconSize: {
      type: String,
      default: '150'
    },
    iconPosition: {
      type: String
    },
    colouredSvg: {
      type: Boolean
    },
    ariaLabel: {
      type: String
    },
    ariaLabelledby: {
      type: String
    },
    ariaControls: {
      type: String
    }
  },
  computed: {
    colourClass() {
      return this.colour ? `link--${this.colour}` : null;
    },
    fontSizeClass() {
      return this.fontSize === 'xsmall' ? 'link--xsmall' : null;
    },
    iconLeadingClass() {
      return this.iconPosition === 'leading' && this.icon
        ? 'leading-icon'
        : null;
    },
    iconTrailingClass() {
      return this.iconPosition === 'trailing' && this.icon
        ? 'trailing-icon'
        : null;
    },
    disabledClass() {
      return this.disabled ? 'link--disabled' : null;
    },
    roleState() {
      return this.button ? 'button' : null;
    },
    disabledState() {
      return this.button && this.disabled ? 'disabled' : null;
    },
    controlState() {
      return this.ariaControls ? this.ariaControls : null;
    },
    labelState() {
      return this.button && !this.ariaLabel && this.ariaLabel
        ? this.ariaLabel
        : null;
    },
    labelledByState() {
      return this.button && !this.ariaLabel && this.ariaLabelledby
        ? this.ariaLabelledby
        : null;
    }
  },
  methods: {
    handleHyperlinkClick(event) {
      if (this.button && this.disabled) {
        event.preventDefault();
      } else {
        this.$emit('click', event);
      }
    }
  }
};
</script>
