<template>
  <div
    :class="[
      'input-wrap',
      typeClass,
      'input-wrap--trailing-icon',
      labelledClass
    ]"
  >
    <input
      ref="searchField"
      type="search"
      :id="id"
      :placeholder="placeholder"
      :disabled="disabledState"
      @blur="handleInputFieldBlur()"
      @change="handleInputFieldChange()"
      @input="handleInputFieldInput()"
      v-model="inputValue"
    />
    <ikea-label :html-for="id" :text="label"></ikea-label>
    <div class="search-actions">
      <ikea-button
        icon="cross-24"
        @click="clearInput()"
        colour="black"
        type="plain"
        text="Clear"
        icon-position=""
        :disabled="disabledState"
      ></ikea-button>
      <div v-if="type === 'filled-white'" class="search-action-divider"></div>
      <ikea-button
        :loading="loading"
        :icon="buttonIcon"
        :text="buttonText"
        :aria-label="label"
        icon-position=""
        :type="buttonType"
        :disabled="disabledState"
        @click="handleSearchClick($event)"
      ></ikea-button>
    </div>
  </div>
</template>

<script>
import Label from './../label';
import Button from './../button';
export default {
  name: 'search-field',
  components: {
    'ikea-label': Label,
    'ikea-button': Button
  },
  props: {
    id: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: true
    },
    disabled: Boolean,
    type: {
      type: String,
      required: true
    },
    iconClickDescription: String,
    defaultValue: String,
    loading: Boolean
  },
  data() {
    return {
      inputValue: '',
      labelled: false
    };
  },
  watch: {
    inputValue() {
      this.labelled = this.inputValue !== '' ? true : false;
    },
    value() {
      this.inputValue = this.value;
    }
  },
  computed: {
    typeClass() {
      return (
        (this.type === 'filled-blue' || this.type === 'filled-blue-text'
          ? 'input-wrap--filled-blue-search'
          : null) ||
        (this.type === 'filled-white'
          ? 'input-wrap--filled-white-search'
          : null)
      );
    },
    labelledClass() {
      return this.labelled ? 'input-wrap--labelled' : null;
    },
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    buttonIcon() {
      return this.type !== 'filled-blue-text' ? 'search-24' : null;
    },
    buttonText() {
      return this.type === 'filled-blue-text' ? this.label : '';
    },
    buttonType() {
      return this.type === 'filled-blue' || this.type === 'filled-blue-text'
        ? 'search'
        : 'search-white';
    }
  },
  methods: {
    clearInput() {
      this.inputValue = '';
    },
    handleInputFieldChange() {
      this.$emit('change', this.inputValue);
    },
    handleInputFieldInput() {
      this.$emit('input', this.inputValue);
    },
    handleInputFieldBlur() {
      this.$emit('blur', this.inputValue);
    },
    handleSearchClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>
