<template>
  <div :class="['input-wrap', iconPositionClass, trailingClass, labelledClass]">
    <the-mask
      v-if="mask"
      ref="input"
      :type="passwordVisible ? 'text' : type"
      :id="id"
      :placeholder="placeholder"
      :disabled="disabledState"
      :required="requiredState"
      v-model="inputValue"
      :maxlength="localMaxLength"
      :name="name"
      @blur.native="handleInputFieldBlur"
      @change.native="handleInputFieldChange"
      @input.native="handleInputFieldInput"
      @focus.native="handleInputFieldFocus"
      :mask="mask"
      :masked="returnMasked"
    />
    <input
      v-else
      ref="input"
      :type="passwordVisible ? 'text' : type"
      :id="id"
      :placeholder="placeholder"
      :disabled="disabledState"
      :required="requiredState"
      v-model="inputValue"
      :maxlength="localMaxLength"
      :name="name"
      @blur="handleInputFieldBlur"
      @change="handleInputFieldChange"
      @input="handleInputFieldInput"
      @focus="handleInputFieldFocus"
    />
    <ikea-label :html-for="id" :text="label" :req-text="req"></ikea-label>
    <ikea-button
      v-if="type === 'password'"
      :icon="passwordVisible ? 'visibility-hide-24' : 'visibility-show-24'"
      @click="passwordVisible = !passwordVisible"
      type="plain"
      :text="passwordVisible ? 'Hide password' : 'Show password'"
      :disabled="disabledState"
    ></ikea-button>
    <ikea-button
      v-if="iconPosition === 'trailing' && icon"
      :icon="icon"
      type="plain"
      :text="iconClickDescription"
      :disabled="disabledState"
      @click="handleInputFieldClick($event)"
    ></ikea-button>
    <icon
      v-if="iconPosition === 'leading' && icon"
      :icon="icon"
      colour="black"
    ></icon>
  </div>
</template>

<script>
import Label from './../label';
import Icon from './../icon';
import Button from './../button';
import { TheMask } from 'vue-the-mask';

export default {
  name: 'input-field',
  components: {
    'ikea-label': Label,
    Icon,
    'ikea-button': Button,
    TheMask
  },
  props: {
    id: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String
    },
    req: {
      type: String
    },
    maxLength: {
      type: [Number, String]
    },
    type: {
      type: String,
      required: true
    },
    disabled: {
      type: Boolean
    },
    icon: {
      type: String
    },
    iconClickDescription: {
      type: String
    },
    iconPosition: {
      type: String
    },
    value: {
      type: String,
      default: ''
    },
    mask: {
      type: String,
      default: ''
    },
    returnMasked: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      passwordVisible: false,
      labelled: false,
      trailing: this.type === 'password' || this.iconPosition === 'trailing',
      inputValue: '',
      localMaxLength: null
    };
  },
  watch: {
    inputValue() {
      this.labelled = this.inputValue !== '' && this.inputValue !== null;
    },
    value() {
      this.inputValue = this.value;
    }
  },
  mounted() {
    if (this.type === 'password' || this.iconPosition === 'trailing') {
      this.trailing = true;
    }

    this.localMaxLength = Number(this.maxLength);
    this.inputValue = this.value;
  },
  computed: {
    iconPositionClass() {
      return this.iconPosition === 'leading'
        ? 'input-wrap--leading-icon'
        : null;
    },
    trailingClass() {
      return this.trailing ? 'input-wrap--trailing-icon' : null;
    },
    labelledClass() {
      return this.labelled ? 'input-wrap--labelled' : null;
    },
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    requiredState() {
      return this.req ? 'required' : null;
    }
  },
  methods: {
    handleInputFieldChange() {
      this.$emit('change', this.inputValue);
    },
    handleInputFieldInput() {
      this.$emit('input', this.inputValue);
    },
    handleInputFieldBlur() {
      this.$emit('blur', this.inputValue);
    },
    handleInputFieldFocus() {
      this.$emit('focus', this.inputValue);
    },
    handleInputFieldClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>

<style src="./styles.scss" lang="scss"></style>
