<template>
  <button
    :class="[
      'btn',
      className,
      typeClass,
      lightClass,
      loadingClass,
      iconPositionClass,
      mobileFullWidthClass
    ]"
    :disabled="disabledState"
    @click="handleButtonClick($event)"
    :type="htmlType"
  >
    <span v-if="type === 'icon' && icon" class="btn__icon-bg"></span>
    <span v-if="icon || text" class="btn__copy">
      <icon v-if="withLeadingIcon" :icon="icon"></icon>
      <span v-if="text" class="btn__label" v-html="text"></span>
      <icon v-if="withTrailingIcon" :icon="icon"></icon>
    </span>
    <slot></slot>
    <loading-spinner
      v-if="loading"
      :light="loadingSpinnerLight"
    ></loading-spinner>
  </button>
</template>

<script>
import Icon from './../icon';
import LoadingSpinner from './../loading-spinner';

export default {
  name: 'ikea-button',
  components: {
    Icon,
    LoadingSpinner
  },
  props: {
    htmlType: {
      type: String,
      default: 'button'
    },
    text: {
      type: String
    },
    icon: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    light: {
      type: Boolean
    },
    type: {
      type: String,
      required: true
    },
    className: {
      type: String
    },
    loading: {
      type: Boolean
    },
    mobileFullWidth: {
      type: Boolean
    },
    iconPosition: {
      type: String
    }
  },
  computed: {
    typeClass() {
      return this.type ? `btn--${this.type}` : null;
    },
    lightClass() {
      return this.light ? `btn--${this.type}-light` : null;
    },
    loadingClass() {
      return this.loading ? 'btn--loading' : null;
    },
    iconPositionClass() {
      return this.iconPosition ? `${this.iconPosition}-icon` : null;
    },
    mobileFullWidthClass() {
      return this.mobileFullWidth ? 'btn--mobile-100' : null;
    },
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    withLeadingIcon() {
      return (
        this.type === 'icon' ||
        this.type === 'plain' ||
        this.type === 'search' ||
        this.type === 'search-white' ||
        (this.icon && this.iconPosition === 'leading')
      );
    },
    withTrailingIcon() {
      return this.icon && this.iconPosition === 'trailing';
    },
    loadingSpinnerLight() {
      return !(
        (!this.light && this.type === 'outlined') ||
        (this.light && this.type !== 'outlined' && this.type !== 'danger') ||
        this.type === 'text' ||
        this.type === 'icon'
      );
    }
  },
  methods: {
    handleButtonClick(event) {
      this.$emit('click', event);
    }
  }
};
</script>
