<template>
  <div role="group">
    <template v-for="(item, index) in list">
      <checkbox
        :key="index"
        :id="item.id"
        :name="item.name"
        :value="item.value"
        :text="item.text"
        :checked="item.checked"
        :disabled="item.disabled"
        :label-link="item.labelLink"
        :label-link-text="item.labelLinkText"
        @change="handleCheckboxGroupChange($event, item)"
      ></checkbox>
    </template>
  </div>
</template>

<script>
import Checkbox from './../checkbox';

export default {
  name: 'checkbox-group-flex',
  components: {
    Checkbox
  },
  props: {
    name: {
      type: String
    },
    list: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      checkedValues: []
    };
  },
  watch: {
    list() {
      // list update delete old checkedValues
      this.checkedValues = [];
    }
  },
  methods: {
    handleCheckboxGroupChange(event, item) {
      if (this.checkedValues.indexOf(event.value) === -1) {
        item.checked = true;
        this.checkedValues.push(event.value);
      } else {
        item.checked = false;
        this.checkedValues.splice(this.checkedValues.indexOf(event.value), 1);
      }
      this.$emit('change', this.checkedValues);
    }
  }
};
</script>
