<template>
  <div class="lightbox" role="presentation">
    <div
      :class="overlayClass"
      @click="modalType !== 'alert' ? handleOnClick($event) : null"
      role="dialog"
    >
      <modal-window
        :is-open="isOpen"
        :title="title"
        :modal-type="modalType"
        :modal-btns="modalBtns"
        :light="light"
        @opened="handleModalOpened()"
        @closed="handleModalClosed()"
        @close-modal="closeModal"
      >
        <slot></slot>
      </modal-window>
    </div>
  </div>
</template>

<script>
import ModalWindow from './../modal-window';
export default {
  name: 'lightbox',
  components: {
    ModalWindow
  },
  props: {
    light: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean
    },
    modalType: {
      type: String
    },
    modalBtns: {
      type: Array
    },
    title: {
      type: String
    }
  },
  data() {
    return {
      isOpen: false
    };
  },
  watch: {
    visible() {
      if (this.visible) {
        this.enter();
      } else {
        this.leave();
      }
    }
  },
  computed: {
    overlayClass() {
      return this.isOpen
        ? `lightbox--open lightbox--${this.light ? 'light' : 'dark'}`
        : '';
    }
  },
  mounted() {
    if (this.visible) {
      this.enter();
    }
  },
  methods: {
    handleOnClick(event) {
      if (event && event.target.getAttribute('role') !== 'dialog') {
        return;
      }
      this.leave();
    },
    escFunction(event) {
      if (event.keyCode === 27) {
        this.leave();
      }
    },
    enter() {
      this.isOpen = true;
    },
    leave() {
      this.isOpen = false;
    },
    handleModalOpened() {
      this.$emit('opened');
    },
    handleModalClosed() {
      this.$emit('closed');
    },
    closeModal() {
      this.leave();
    }
  },
  created() {
    if (this.modalType !== 'alert') {
      document.addEventListener('keydown', this.escFunction, false);
    }
  },
  destroyed() {
    document.removeEventListener('keydown', this.escFunction, false);
  }
};
</script>
