<template>
  <div :class="['input-wrap', 'input-wrap--trailing-icon', loadingClass]">
    <select
      ref="select"
      v-model="selected"
      :id="id"
      :disabled="disabled"
      @change="handleDropdownChange()"
    >
      <template v-if="!isGroup" v-for="(item, index) in dropdownItems">
        <dropdown-item
          :key="index"
          :value="item.value"
          :name="item.name"
          :disabled="item.disabled"
        ></dropdown-item>
      </template>
      <template v-if="isGroup" v-for="(item, index) in dropdownItems">
        <dropdown-group
          v-if="item.groupName"
          :key="index"
          :group-name="item.groupName"
          :group-items="item.groupItems"
        ></dropdown-group>
        <dropdown-item
          v-else
          :key="index"
          :value="item.value"
          :name="item.name"
          :disabled="item.disabled"
        ></dropdown-item>
      </template>
    </select>
    <ikea-label :html-for="id" :text="label" :req-text="req"></ikea-label>
    <icon icon="chevron-down-24"></icon>
  </div>
</template>

<script>
import Icon from './../icon';
import Label from './../label';
import DropdownItem from './DropdownItem';
import DropdownGroup from './DropdownGroup';

export default {
  name: 'dropdown',
  components: {
    Icon,
    'ikea-label': Label,
    DropdownItem,
    DropdownGroup
  },
  props: {
    id: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    req: {
      type: String
    },
    disabled: {
      type: Boolean
    },
    size: {
      type: String
    },
    selectedItem: {
      type: String
    },
    isGroup: {
      type: Boolean,
      default: false
    },
    dropdownItems: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      labelled: null,
      selected: this.selectedItem
    };
  },
  watch: {
    selected() {
      this.labelled =
        this.selected !== null &&
        this.selected !== '' &&
        this.selected !== undefined
          ? true
          : false;
    }
  },
  mounted() {
    this.labelled =
      this.selected !== null &&
      this.selected !== '' &&
      this.selected !== undefined
        ? true
        : false;
  },
  computed: {
    loadingClass() {
      return this.labelled ? 'input-wrap--labelled' : null;
    }
  },
  methods: {
    handleDropdownChange() {
      this.$emit('change', this.selected);
    }
  }
};
</script>
