<template>
  <div :class="['alert', lightClass, `alert--${type}`, sizeClass]">
    <div class="alert__icon">
      <div class="alert__icon-style">
        <icon :icon="sprite" :size="iconSize"></icon>
      </div>
    </div>
    <div class="alert__container">
      <div class="alert__text">
        <alert-text
          :title="title"
          :message="message"
          :messages="messages"
        ></alert-text>
        <alert-list-items
          v-if="items && items.length > 0"
          :items="items"
        ></alert-list-items>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from './../icon';
import AlertText from './AlertText';
import AlertListItems from './AlertListItems';

export default {
  name: 'inline-alert',
  components: {
    Icon,
    AlertText,
    AlertListItems
  },
  props: {
    type: {
      type: String,
      required: true
    },
    title: {
      type: String
    },
    size: {
      type: String
    },
    message: {
      type: String
    },
    messages: {
      type: Array
    },
    items: {
      type: Array
    },
    version: {
      type: String,
      default: 'expressive'
    }
  },
  data() {
    return {
      sprite: '',
      iconSize: '',
      light: this.version === 'subtle'
    };
  },
  computed: {
    lightClass() {
      return this.light ? 'alert--light' : null;
    },
    sizeClass() {
      return this.size ? `alert--${this.size}` : 'alert--m';
    }
  },
  mounted() {
    if (this.type === 'error' && this.version === 'expressive') {
      this.sprite = `message-${this.type}-bg-inverted`;
    } else {
      this.sprite = `message-${this.type}-bg`;
    }

    if (this.size === 'l') {
      this.iconSize = '250';
    } else if (this.size === 's') {
      this.iconSize = '100';
    } else {
      this.iconSize = '150';
    }

    switch (this.size) {
      case 's':
        this.sprite += '-16';
        break;
      default:
        this.sprite += '-24';
        break;
    }
  }
};
</script>
