<template>
  <div class="input-wrap quantity">
    <ikea-button
      icon="minus-24"
      type="plain"
      @click="decrease($event)"
      :disabled="disabledState || disabledDescreaseState"
      class="quantity__decrease"
      :aria-label="decreaseBtnTxt"
    ></ikea-button>
    <input
      type="number"
      pattern="[0-9]*"
      :id="id"
      :aria-label="id"
      :disabled="disabledState"
      v-model="inputValue"
      :max="max"
      @change="handleQuantityFieldChange()"
      @keydown="keyDownIncrease($event)"
    />
    <ikea-button
      icon="plus-24"
      type="plain"
      @click="increase($event)"
      :disabled="disabledState || disabledIncreasedState"
      class="quantity__increase"
      :aria-label="increaseBtnTxt"
    ></ikea-button>
  </div>
</template>

<script>
import Button from './../button';
export default {
  name: 'quantity-field',
  components: {
    'ikea-button': Button
  },
  props: {
    id: {
      type: String,
      required: true
    },
    disabled: Boolean,
    max: {
      type: Number,
      required: true
    },
    value: {
      type: [Number, String],
      required: true
    },
    increaseBtnTxt: {
      type: String,
      default: 'Increase'
    },
    decreaseBtnTxt: {
      type: String,
      default: 'Decrease'
    }
  },
  data() {
    return {
      inputValue: this.value,
      decreaseBtn: false,
      increaseBtn: false
    };
  },
  computed: {
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    disabledDescreaseState() {
      return this.decreaseBtn ? 'disabled' : null;
    },
    disabledIncreasedState() {
      return this.increaseBtn ? 'disabled' : null;
    }
  },
  watch: {
    value() {
      this.inputValue = this.value;
      const re = /^[0-9]\d*$/;
      if (
        (this.inputValue === '' || re.test(this.inputValue)) &&
        this.inputValue <= this.max
      ) {
        this.increaseBtn = this.inputValue >= this.max;
        this.decreaseBtn = this.inputValue <= 0;
      }
    }
  },
  mounted() {
    this.inputValue = this.value;
  },
  methods: {
    decrease(event) {
      event.preventDefault();
      if (!this.disabled) {
        this.inputValue =
          this.inputValue <= 0 ? this.inputValue : this.inputValue - 1;
        this.decreaseBtn = this.inputValue <= 0;
        this.increaseBtn = false;

        this.handleQuantityFieldChange();
      }
    },
    increase(event) {
      event.preventDefault();
      if (!this.disabled) {
        this.inputValue =
          this.inputValue >= this.max ? this.inputValue : ++this.inputValue;
        this.increaseBtn = this.inputValue >= this.max;
        this.decreaseBtn = false;

        this.handleQuantityFieldChange();
      }
    },
    keyDownIncrease(event) {
      if (event.keyCode === 38) {
        this.increase(event);
      } else if (event.keyCode === 40) {
        this.decrease(event);
      }
    },
    handleQuantityFieldChange() {
      if (this.inputValue > this.max) {
        this.inputValue = this.max;
      }
      this.$emit('change', this.inputValue);
    }
  }
};
</script>
