<template>
  <fieldset role="group">
    <legend v-if="name">{{ name }}</legend>
    <template v-for="(item, index) in list">
      <radio-button
        :key="index"
        :id="item.id"
        :name="item.name"
        :value="item.value"
        :text="item.text"
        :checked="item.checked"
        :disabled="item.disabled"
        :label-link="item.labelLink"
        :label-link-text="item.labelLinkText"
        @change="handleRadioButtonGroupChange($event, item)"
      ></radio-button>
    </template>
  </fieldset>
</template>

<script>
import RadioButton from './../radio-button';

export default {
  name: 'radio-button-group',
  components: {
    RadioButton
  },
  props: {
    name: {
      type: String
    },
    list: {
      type: Array,
      required: true
    }
  },
  methods: {
    handleRadioButtonGroupChange(event, item) {
      this.list.forEach(element => {
        element.checked = false;
      });
      item.checked = true;

      this.$emit('change', event);
    }
  }
};
</script>
