<template>
  <div class="checkbox">
    <input
      ref="checkbox"
      type="checkbox"
      :value="value"
      :name="name"
      :id="id"
      :disabled="disabledState"
      :checked="checkedState"
      @change="handleCheckboxChange($event)"
    />
    <span class="checkbox__symbol">
      <span class="checkbox__box-highlight"></span>
      <span class="checkbox__box">
        <icon class="checkbox__checkmark" icon="checkmark-24"></icon>
      </span>
    </span>
    <span class="checkbox__label">
      <ikea-label :for="id" :text="text" :req-text="''"></ikea-label>
      <template v-if="labelLink && labelLinkText">
        <hyperlink
          :text="labelLinkText"
          :url="labelLink"
          :target="labelTarget"
        ></hyperlink
      ></template>
    </span>
  </div>
</template>

<script>
import Icon from './../icon';
import Label from './../label';
import Hyperlink from './../hyperlink';

export default {
  name: 'checkbox',
  components: {
    Icon,
    'ikea-label': Label,
    Hyperlink
  },
  props: {
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    value: {
      type: String
    },
    text: {
      type: String,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    labelLink: {
      type: String
    },
    labelLinkText: {
      type: String
    },
    labelTarget: {
      type: String
    }
  },
  computed: {
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    checkedState() {
      return this.checked ? 'checked' : null;
    }
  },
  methods: {
    handleCheckboxChange(event) {
      this.$emit('change', event.target);
    }
  }
};
</script>
