<template>
  <div :class="['input-wrap', labelledClass]">
    <textarea
      :class="maxHeightClass"
      ref="textarea"
      :id="id"
      :aria-describedby="describedById"
      :placeholder="placeholder"
      :disabled="disabledState"
      :required="requiredState"
      :maxlength="localMaxLength"
      @change="handleTextAreaChange()"
      @input="handleTextAreaInput()"
      @blur="handleTextAreaBlur()"
      v-model="inputValue"
      :style="{ height: height + 'px', localMaxHeight: localMaxHeight + 'px' }"
    ></textarea>
    <ikea-label :html-for="id" :text="label" :req-text="req"></ikea-label>
  </div>
</template>

<script>
import Label from './../label';
export default {
  name: 'text-area',
  components: {
    'ikea-label': Label
  },
  props: {
    id: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    data: String,
    placeholder: String,
    value: String,
    req: String,
    maxLength: {
      type: [String, Number]
    },
    disabled: Boolean,
    baseHeight: {
      type: [String, Number],
      default: 64
    },
    maxHeight: {
      type: [String, Number],
      default: 328
    },
    describedById: String
  },
  data() {
    return {
      labelled: false,
      inputValue: '',
      height: null,
      localMaxHeight: null,
      localMaxLength: null,
      localBaseHeight: null
    };
  },
  watch: {
    inputValue() {
      this.labelled = this.inputValue !== '' ? true : false;

      this.recalculateHeight();
    },
    value() {
      this.inputValue = this.value;
    }
  },
  computed: {
    labelledClass() {
      return this.labelled ? 'input-wrap--labelled' : null;
    },
    maxHeightClass() {
      return this.localMaxHeight < this.height ? 'textarea--overflow' : null;
    },
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    requiredState() {
      return this.req ? 'required' : null;
    }
  },
  mounted() {
    this.inputValue = this.value;
    this.localMaxHeight = Number(this.maxHeight);
    this.localMaxLength = Number(this.maxLength);
    this.localBaseHeight = Number(this.baseHeight);

    this.$nextTick(() => {
      this.recalculateHeight();
    });
  },
  methods: {
    handleTextAreaChange() {
      this.$emit('change', this.inputValue);
    },
    handleTextAreaInput() {
      this.$emit('input', this.inputValue);
    },
    handleTextAreaBlur() {
      this.$emit('blur', this.inputValue);
    },
    recalculateHeight() {
      const heightLocal = this.$refs.textarea.scrollHeight;
      this.height = Math.max(heightLocal, this.localBaseHeight);
    }
  }
};
</script>
