<template>
  <div class="radio">
    <input
      ref="radioButton"
      type="radio"
      :value="value"
      :name="name"
      :id="id"
      :disabled="disabledState"
      :checked="checkedState"
      @change="handleRadioChange"
    />
    <span class="radio__symbol">
      <span class="radio__fill-highlight"></span>
      <span class="radio__fill"></span> <span class="radio__fill-dot"></span>
    </span>
    <span class="radio__label">
      <ikea-label :for="id" :text="text" :req-text="''"></ikea-label>
      <template v-if="labelLink && labelLinkText">
        <hyperlink :text="labelLinkText" :url="labelLink"></hyperlink
      ></template>
    </span>
  </div>
</template>

<script>
import Label from './../label';
import Hyperlink from './../hyperlink';

export default {
  name: 'radio-button',
  components: {
    'ikea-label': Label,
    Hyperlink
  },
  props: {
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    value: {
      type: String
    },
    text: {
      type: String,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    labelLink: {
      type: String
    },
    labelLinkText: {
      type: String
    }
  },
  computed: {
    disabledState() {
      return this.disabled ? 'disabled' : null;
    },
    checkedState() {
      return this.checked ? 'checked' : null;
    }
  },
  methods: {
    handleRadioChange(event) {
      this.$emit('change', event.target.value);
    }
  }
};
</script>
