<template>
  <div>
    <h4 class="alert__title" v-if="title" :key="title" v-html="title"></h4>
    <template
      v-if="messages && messages.length > 0"
      v-for="(message, index) in messages"
    >
      <p :key="index" v-html="message"></p>
    </template>
    <p v-if="message" :key="message" v-html="message"></p>
  </div>
</template>

<script>
export default {
  name: 'alert-text',
  props: {
    title: {
      type: String
    },
    message: {
      type: String
    },
    messages: {
      type: Array
    }
  }
};
</script>
